export default {
  name: 'Experience',
  data() {
    return {
      experience: [],
      activeTab: null
    };
  },
  created() {
    this.getAbout().then(() => this.setActiveTab());
  },
  methods: {
    async getAbout() {
      const experience = await this.$content('experience')
        .sortBy('updatedAt')
        .fetch();

      this.experience = experience;
    },
    setActiveTab() {
      if (this.experience.length > 0) {
        this.activeTab = this.experience[0].company;
      }
    }
  }
};
