export default {
  name: 'About',
  data() {
    return {
      about: null
    };
  },
  computed: {
    description() {
      const about = this.about;
      let description;

      if (about !== null) {
        description = about.description.split('\n');
      }

      return description;
    }
  },
  created() {
    this.getAbout();
  },
  methods: {
    async getAbout() {
      const about = await this.$content('about').fetch();

      this.about = about;
    }
  }
};
