export default {
  name: 'Contact',
  data() {
    return {
      status: null,
      busy: false,
      form: {
        name: null,
        email: null,
        subject: null,
        message: null
      }
    };
  },
  methods: {
    encode(data) {
      const formData = new FormData();

      for (const key of Object.keys(data)) {
        formData.append(key, data[key]);
      }

      return new URLSearchParams(formData).toString();
    },

    submitForm() {
      this.status = null;
      this.busy = true;

      fetch('/', {
        method: 'POST',
        headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
        body: this.encode({
          'form-name': 'contact',
          ...this.form
        })
      })
        .then(() => {
          this.status = 'success';
          this.busy = false;

          this.form.name = null;
          this.form.email = null;
          this.form.subject = null;
          this.form.message = null;
        })
        .catch((err) => {
          this.status = 'error';
          this.busy = false;

          // eslint-disable-next-line no-console
          console.error(err);
        });
    }
  }
};
