export default {
  name: 'Skill',
  data() {
    return {
      skills: null
    };
  },
  created() {
    this.getSkills();
  },
  methods: {
    async getSkills() {
      const skills = await this.$content('skills').fetch();

      this.skills = skills;
    }
  }
};
